import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { ROOTROUTES_DEFINES,  GlobalVarService } from '@ngxsuit';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    providers: [FormBuilder]
})
export class LoginComponent implements OnInit {
    formGroup:FormGroup;
    private csrf_token:string;
    constructor(public router: Router, private formbuilder:FormBuilder, private http:HttpClient, private gv:GlobalVarService) { }

    ngOnInit() {
        this.csrf_token = this.gv.getString('csrf_token');
        this.formGroup = this.formbuilder.group({
            "email":['', [Validators.required, Validators.email] ],
            "password":['', [Validators.required, Validators.minLength(8)]],
        });
    }

    onLoggedin() {
        this.http.post(this.gv.getString('admin_login'), {
            _token:this.csrf_token,
            email:this.formGroup.value['email'],
            password:this.formGroup.value['password'],
            is_ajax:1
        }, { withCredentials: true }).subscribe(data => {
            if (data['success'] !== undefined && data['success']) {
                let expire_at:number = Math.ceil((new Date()).getTime()/1000);
                expire_at = expire_at + 60 * (+this.gv.getString('session_lifetime'));
                localStorage.setItem('admin_login', JSON.stringify({
                        email:this.formGroup.value['email'],
                        expire: expire_at,
                        username: data['username']
                    })
                );
                this.router.navigate(['/']);
            } else {
                this.csrf_token = data['csrf_token'];
            }
        });
    }
}



